<template>
	<div id="login">
		<div class="header-box text-box-color text-title-color">
			<h1>
				{{state.title || ""}}bilibili
			</h1>
		</div>

		<div class="form-group">
			<form>
				<ul class="list-group list-group-border">
					<li class="list-group-li" style="display: flex;">
						<div class="input-item clearfix text-box-color">
							<span class="float-left input-title text-title-color"></span>
							<input placeholder="" v-model="phone" type="text" name="username" autocomplete="on" class="float-left input-text text-title-color long-width">
							<div class="float-left input-status clearfix">
								<span data-v-15db3d56="" @click="phone=''"></span>
								<!---->
							</div>
							<!---->
						</div>
					</li>
					<li class="list-group-li" style="display: flex;">
						<div class="input-item clearfix text-box-color">
							<span class="float-left input-title text-title-color"></span>
							<input placeholder="" v-model="password" maxlength="20" type="password" autocomplete="on" class="float-left input-text text-title-color long-width">
							<div class="float-left input-status clearfix">
								<span data-v-15db3d56="" @click="password=''"></span>
								<!---->
							</div>
							<!---->
						</div>
					</li>
					<li class="list-group-li" style="display: flex;" v-show="state.state">
						<div class="input-item clearfix text-box-color">
							<span class="float-left input-title text-title-color"></span>
							<input placeholder="" v-model="password2" maxlength="20" type="password" autocomplete="on" class="float-left input-text text-title-color long-width">
							<div class="float-left input-status clearfix">
								<span data-v-15db3d56="" @click="password2=''"></span>
								<!---->
							</div>
							<!---->
						</div>
					</li>
					<li class="list-group-li" style="display: flex;">
						<div class="input-item clearfix text-box-color codePut">
							<span class="float-left input-title text-title-color"></span>
							<input placeholder="" v-model="code" style="width: 50%;" maxlength="20" type="password" autocomplete="on"
							 class="float-left input-text text-title-color long-width">

							<!---->
						</div>
						<div class="codeImg">
							<img class="code" @click="getCode()" :src="smsCode.imgUrl" />
						</div>
					</li>
				</ul>
			</form>
		</div>
		<div class="forget-group">
			<p><a href="javascript:;" class="pink-color"></a></p>
		</div>
		<div class="btn-box clearfix">
			<a href="javascript:;" class="btn-primary btn-width" @click="switchover()">{{state.btn1 || ""}}</a>
			<div class="btn-default btn-width btn-default-disable" @click="toLogin()">{{state.btn2 || ""}}</div>
		</div>
		<p class="explain tips"><a href="javascript:;"></a>
			<a href="javascript:;"></a></p>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				state: {},
				login: {
					state: 0,
					title: '',
					btn1: "",
					btn2: ""
				},
				enroll: {
					state: 1,
					title: '',
					btn1: "",
					btn2: ""
				},
				phone: '',
				password: '',
				password2: '',
				code: '',
				smsCode: ''
			}
		},
		created() {
			this.state = this.login;
			this.getCode()
		},
		methods: {
			switchover() {
				this.state.state == 0 ? this.state = this.enroll : this.state = this.login;
				this.phone= ''
				this.password= ''
				this.password2= ''
				this.code= ''
			},
			toLogin() {
				if (this.state.state == 0) {
					// 
					if (this.phone == '') {
						this.$toast("");
					} else if (this.password == '') {
						this.$toast("")
					} else if (this.smsCode.code != this.code) {
						this.$toast("")
						this.getCode();
					} else {
						let str = localStorage.getItem('user')
						var userList = Array()
						if (str) {
							userList = JSON.parse(str)
						}
						for (var obj in userList) {
							var temp = userList[obj]
							if (temp.phone == this.phone) {
								if (temp.password == this.password) {
									sessionStorage.setItem('username', temp.username)
									this.$router.push('/')
									return
								} else {
									this.$toast("")
									return
								}
							}
						}
						this.$toast('')
					}
				} else {
					// 
					if (this.username == '') {
						this.$toast('')
					} else if (this.password == '') {
						this.$toast('')
					} else if (this.phone == '') {
						this.$toast('')
					} else {
						// let str = sessionStorage.getItem('smsCode')
						// let smsCode = JSON.parse(str)
						if (this.password != this.password2) {
							this.$toast('')
						} else if (this.smsCode.code != this.code) {
							this.$toast("")
							this.getCode();
						} else {
							let str = localStorage.getItem('user')
							var userList = Array()
							if (str) {
								userList = JSON.parse(str)
							}
							for (var obj in userList) {
								var temp = userList[obj]
								if (temp.username == this.username) {
									this.$toast('')
									return
								}
								if (temp.phone == this.phone) {
									this.$toast('')
									return
								}
							}
							let user = {
								username: this.username,
								password: this.password,
								phone: this.phone
							}
							userList.push(user)
							localStorage.setItem('user', JSON.stringify(userList))
							alert('')
							this.switchover()
							this.getCode()
							// this.$router.push('/login')
						}
					}
				}
			},
			getCode() {
				this.HttpService.get('/code', res => {
					this.smsCode = res
				}, err => {
					console.error(err)
				})
			}
		},

	}
</script>

