<template>
	<div class="login">
		<TopBanner></TopBanner>
		<div class="title">
			<div><span></span></div>
		</div>
		<div class="login-box">
			<div>
				<div class="login-box-left">
					<div class="QR-code-top"><img src="../../assets/img/e6f2388d454c82004905753802e9cfe709d80d08.png" /></div>
					<div class="QR-code">
						<img
							src="data:image/png;base64,"
							style="display: block;"
						/>
					</div>
					<div class="login-box-left-footer">
						<p></p>
						<p>
							
							<a href="javascript:;"></a>
							<br />
							
							<br />
							APP
						</p>
					</div>
				</div>
				<div class="login-box-mid"></div>
				<div class="login-box-right">
					<div>
						<div class="login-type">
							<router-link to="/login"></router-link>
							<a href="#"></a>
						</div>
						<div class="username">
							<input type="text" v-model="phone" placeholder=""/>
							<span>{{phoneError}}</span>
						</div>
						<div class="password">
							<input type="password" v-model="password" placeholder=""/>
							<span>{{passwordError}}</span>
						</div>
						<div class="remember">
							<label>
								<input type="checkbox" />
								<span></span>
								<span class="prompt"></span>
							</label>
							<div>
								<a href="javascript:;"></a>
								<a href="javascript:;"></a>
							</div>
						</div>
						<div class="btn">
							<button class="login-btn" @click="login"></button>
							<button class="register-btn" @click="goRegisterPage"></button>
						</div>
						<div class="other-login">
							<a href="javascript:;" class="web"></a>
							<a href="javascript:;" class="qq">QQ</a>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="agree">
			<p>
				
				<a href="javascript:;"></a>
				
				<a href="javascript:;"></a>
			</p>
		</div>
		<Footer></Footer>
	</div>
</template>

<script>
import TopBanner from '@/components/TopBanner.vue';
import Footer from '@/components/Footer.vue';
export default {
	name: 'Login',
	data(){
		return{
			phone:'',
			password:'',
			phoneError:'',
			passwordError:''
		}
	},
	methods:{
		login(){
			this.clearError()
			if(this.phone == ''){
				this.phoneError = ''
			}else if(this.password == ''){
				this.passwordError = ''
			}else{
				let str = localStorage.getItem('user')
				var userList = Array()
				if(str){
					userList = JSON.parse(str)
				}
				for(var obj in userList){
					var temp = userList[obj]
					if(temp.phone == this.phone){
						if(temp.password == this.password){
							sessionStorage.setItem('username',temp.username)
							this.$router.push('/')
							return
						}else{
							this.passwordError = ''
							return
						}
					}
				}
				this.phoneError = ''
			}
		},
		clearError(){
			this.phoneError = ''
			this.passwordError = ''
		},
		goRegisterPage(){
			this.$router.push('/register')
		}
	},
	components: {
		TopBanner,
		Footer
	}
};
</script>

